  padding: 0;
  margin: 0;
}
 
body {
  font-family: 'Poppins', sans-serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
 
 
/*
  AppBar
*/
 
.app-bar {
  padding: 8px 16px;
  display: grid;
  grid-template-columns: 1fr 8fr;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, background-color 0.3s ease;
  background-color: #f96500; 
}

.app-bar.hide {
  transform: translateY(-100%);
}

.app-bar.show {
  transform: translateY(0);
  opacity: 80%;
}
 
.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
  background-color: #f96500;
}
 
.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}
 
.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__brand img{
    width: 65px;
    height: 65px;
}
 
.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: #f96500;
  overflow: hidden;
}
 
.app-bar .app-bar__navigation.open {
  left: 0;
}
 
.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: white;
  padding: 8px;
  margin-bottom: 5px;
  width: 100%;
}

/*
  Banner
*/

.banner {
    position: relative;
    height: 50vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.banner-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-attachment: fixed;
    clip-path: polygon(0 0, 100% 0, 100% 70%, 0 100%);
    z-index: -1;
}

.banner-text {
    position: relative;
    color: black;
    padding: 20px;
    z-index: 1;
}

.banner-text h1 {
    font-size: 3em;
    margin: 0;
    padding: 0;
}

.banner-text p {
    font-size: 1.5em;
    margin: 0;
}

/*
  Sort
*/

.sort{
    display: flex;
    flex-direction: column-reverse;
}

.filter{
    display: grid;
    grid-template-columns: auto auto;
    gap: 20px;
    align-items: center;
    margin-bottom: 20px;
}

.filter select{
    border-radius: 4px;
    padding: 10px;
}

/*
  Detail Containers
*/
.details-container{
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  gap: 20px;
}

/* 
  Item
*/

.item {
  width: 100%;
  padding-bottom: 20px;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.item:hover{
  transform: scale(1.01);
  transition: all 0.5s
}

.detail-item {
  display: grid;
  padding-inline: 5%;
}

.description-item {
  color: #61677A;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.item-picture {
  width: 100%;
  max-height: 195px;
  min-height: 195px;
  object-fit: cover;
  border-radius: 4px;
}

.link-item {
  text-decoration: none;
  color: #fbb72b;
  box-sizing: border-box;
  min-width: 44px;
  min-height: 44px;
}
/*
  Pagination
*/
.pagination{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-top: 40px;
}

.pagination .button{
    background-color: #f96500;
    border: none;
    border-radius: 4px;
    color: white;
    padding: 20px;
}

.pagination button{
    background-color: #edddd3;
    border: none;
    border-radius: 4px;
    color: black;
    padding: 10px;
    cursor: pointer;
}

#page-numbers{
    display: flex;
    gap: 5px;
}

#page-numbers .active{
    display: flex;
    gap: 10px;
    background-color: #f96500;
    color: white;
}
 
/*
  Main Content
*/
 
main {
  padding: 32px;
  flex: 1;
}
 
.content {
  margin: 0 auto;
  min-height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  margin-top: 50px;
}
 
.content .content__heading {
  font-weight: normal;
}

.skip-link {
  font-weight: bold;
  text-decoration: none;
  background-color: #fbb72b;
  position: absolute;
  top: -50px;
  left: 0;
  padding: 8px;
  color: rgb(255, 255, 255);
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}
 
 
/*
  Footer
*/
 
footer {
  padding: 16px;
  background-color: #f96500;
}
 
footer p {
  text-align: center;
  color: white;
}
 
footer p a {
  color: white;
  text-decoration: none;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;EACE,UAAU;EACV,SAAS;AACX;;AAEA;EACE,kCAAkC;EAClC,uBAAuB;EACvB,iBAAiB;EACjB,aAAa;EACb,sBAAsB;AACxB;;;AAGA;;CAEC;;AAED;EACE,iBAAiB;EACjB,aAAa;EACb,8BAA8B;EAC9B,SAAS;EACT,gBAAgB;EAChB,MAAM;EACN,WAAW;EACX,0CAA0C;EAC1C,2DAA2D;EAC3D,yBAAyB;AAC3B;;AAEA;EACE,4BAA4B;AAC9B;;AAEA;EACE,wBAAwB;EACxB,YAAY;AACd;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,yBAAyB;AAC3B;;AAEA;EACE,6BAA6B;EAC7B,YAAY;EACZ,eAAe;EACf,YAAY;EACZ,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,YAAY;EACZ,YAAY;EACZ,oBAAoB;EACpB,YAAY;EACZ,yBAAyB;EACzB,gBAAgB;AAClB;;AAEA;EACE,OAAO;AACT;;AAEA;EACE,qBAAqB;EACrB,qBAAqB;EACrB,YAAY;EACZ,YAAY;EACZ,kBAAkB;EAClB,WAAW;AACb;;AAEA;;CAEC;;AAED;IACI,kBAAkB;IAClB,YAAY;IACZ,gBAAgB;IAChB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,sBAAsB;IACtB,4BAA4B;IAC5B,iDAAiD;IACjD,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,aAAa;IACb,UAAU;AACd;;AAEA;IACI,cAAc;IACd,SAAS;IACT,UAAU;AACd;;AAEA;IACI,gBAAgB;IAChB,SAAS;AACb;;AAEA;;CAEC;;AAED;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,gCAAgC;IAChC,SAAS;IACT,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,aAAa;AACjB;;AAEA;;CAEC;AACD;EACE,aAAa;EACb,0BAA0B;EAC1B,uBAAuB;EACvB,SAAS;AACX;;AAEA;;CAEC;;AAED;EACE,WAAW;EACX,oBAAoB;EACpB,kBAAkB;EAClB,0CAA0C;AAC5C;;AAEA;EACE,sBAAsB;EACtB;AACF;;AAEA;EACE,aAAa;EACb,kBAAkB;AACpB;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,oBAAoB;EACpB,qBAAqB;EACrB,4BAA4B;AAC9B;;AAEA;EACE,WAAW;EACX,iBAAiB;EACjB,iBAAiB;EACjB,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,cAAc;EACd,sBAAsB;EACtB,eAAe;EACf,gBAAgB;AAClB;AACA;;CAEC;AACD;IACI,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;IACT,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,aAAa;IACb,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,SAAS;IACT,yBAA