___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 650px) {
  .sort{
    display: grid;
    grid-template-columns: 1fr auto;
  }

  .filter{
    display: flex;
    gap: 20px;
    align-items: center;
  }

  .filter select{
    border-radius: 4px;
    padding: 10px;
  }

  .content {
    grid-template-columns: 1fr 1fr;
  }

  .app-bar {
    grid-template-columns: 1fr auto;
    padding: 8px 32px;    
  }
 
  .app-bar .app-bar__brand h1 {
    font-size: 1.5em;
  }
 
  .app-bar .app-bar__menu {
    display: none;
  }
 
  .app-bar .app-bar__navigation {
    position: static;
    width: 100%;
  }
 
  .app-bar .app-bar__navigation ul li {
    display: inline-block;
  }
 
  .app-bar .app-bar__navigation ul li a {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 80px;
    text-align: center;
    margin: 0;
  }

  .app-bar .app-bar__navigation ul li a:hover{
      border-bottom: 3px solid white;
  }

  .app-bar .app-bar__navigation ul li a:focus{
      border-bottom: 3px solid white;
  }
}
 
@media screen and (min-width: 800px) {
  .app-bar .app-bar__brand h1 {
    font-size: 2em;
  }
}
 
@media screen and (min-width: 850px) {
  .content {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
 
@media screen and (min-width: 1200px) {
  .app-bar {
    grid-template-columns: 1fr 1fr;
    padding: 8px 80px;
    position: fixed;
    width: 100%;
  }

  .movies {
    grid-template-columns: repeat(4, 1fr);
  }

  .content {
    display: grid;
    gap: 20px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
 
@media screen and (min-width: 1600px) {
  .content {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;EACE;IACE,aAAa;IACb,+BAA+B;EACjC;;EAEA;IACE,aAAa;IACb,SAAS;IACT,mBAAmB;EACrB;;EAEA;IACE,kBAAkB;IAClB,aAAa;EACf;;EAEA;IACE,8BAA8B;EAChC;;EAEA;IACE,+BAA+B;IAC/B,iBAAiB;EACnB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,gBAAgB;IAChB,WAAW;EACb;;EAEA;IACE,qBAAqB;EACvB;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,WAAW;IACX,kBAAkB;IAClB,SAAS;EACX;;EAEA;MACI,8BAA8B;EAClC;;EAEA;MACI,8BAA8B;EAClC;AACF;;AAEA;EACE;IACE,cAAc;EAChB;AACF;;AAEA;EACE;IACE,kCAAkC;EACpC;AACF;;AAEA;EACE;IACE,8BAA8B;IAC9B,iBAAiB;IACjB,eAAe;IACf,WAAW;EACb;;EAEA;IACE,qCAAqC;EACvC;;EAEA;IACE,aAAa;IACb,SAAS;IACT,sCAAsC;EACxC;AACF;;AAEA;EACE;IACE,0CAA0C;EAC5C;AACF","sourcesContent":["@media screen and (min-width: 650px) {\r\n  .sort{\r\n    display: grid;\r\n    grid-template-columns: 1fr auto;\r\n  }\r\n\r\n  .filter{\r\n    display: flex;\r\n    gap: 20px;\r\n    align-items: center;\r\n  }\r\n\r\n  .filter select{\r\n    border-radius: 4px;\r\n    padding: 10px;\r\n  }\r\n\r\n  .content {\r\n    grid-template-columns: 1fr 1fr;\r\n  }\r\n\r\n  .app-bar {\r\n    grid-template-columns: 1fr auto;\r\n    padding: 8px 32px;    \r\n  }\r\n \r\n  .app-bar .app-bar__brand h1 {\r\n    font-size: 1.5em;\r\n  }\r\n \r\n  .app-bar .app-bar__menu {\r\n    display: none;\r\n  }\r\n \r\n  .app-bar .app-bar__navigation {\r\n    position: static;\r\n    width: 100%;\r\n  }\r\n \r\n  .app-bar .app-bar__navigation ul li {\r\n    display: inline-block;\r\n  }\r\n \r\n  .app-bar .app-bar__navigation ul li a {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 20px;\r\n    width: 80px;\r\n    text-align: center;\r\n    margin: 0;\r\n  }\r\n\r\n  .app-bar .app-bar__navigation ul li a:hover{\r\n      border-bottom: 3px solid white;\r\n  }\r\n\r\n  .app-bar .app-bar__navigation ul li a:focus{\r\n      border-bottom: 3px solid white;\r\n  }\r\n}\r\n \r\n@media screen and (min-width: 800px) {\r\n  .app-bar .app